/**
 * +
 */
export function getTwestaPostWithCommentsChain(): string {
  return `[Chain of thought]
<think>
## 1. 
- 
- Chain

## 2. 
- ****: //
- **283pro**: 
- : 

## 3. 
- Twesta
- : ///
- emoji#

## 4. 2-4
- 1-2
- 1-2
- 
- 2-4
</think>
[/Chain of thought]
`;
}

/**
 * +
 */
export function getTwestaPostWithCommentsPrompt(params: {
  idolId: string;
  currentTime: string;
  specialEvent?: string;
  recentPosts?: string;
  relatedIdols?: string[];
  includeAntiFan?: boolean;
  commentCount?: number;
  hasImage?: boolean; // AI 
}): string {
  const idolName = getCharacterName(params.idolId);
  const targetCommentCount = params.commentCount ?? 3;

  // 
  const imagePrompt = params.hasImage
    ? `##  
****
****
- /  
- //  
- /  
****\n\n`
    : '';

  // 
  const commentRules = params.includeAntiFan
    ? `## 
- : ${params.relatedIdols?.map(id => getCharacterName(id)).join('') || '1-2'}
- : 
- ****: 1
${ANTI_FAN_STYLE_GUIDE}
- : ** ${targetCommentCount} **

##  
 rhythmIncrease  1-10:
- 1-3: 
- 4-6: 
- 7-10: `
    : `## 
- : ${params.relatedIdols?.map(id => getCharacterName(id)).join('') || '1-2'}
- : 
- : ****
- : ** ${targetCommentCount} **`;

  return `# Twesta +

## 
- : ${idolName} (${params.idolId})
- : ${params.currentTime}
${params.specialEvent ? `- : ${params.specialEvent}` : ''}

${imagePrompt}## 
1. 
2. ${targetCommentCount}

## 
- P
- 

${commentRules}

${params.recentPosts ? `## \n${params.recentPosts}\n` : ''}

## JSON
\`\`\`json
{
  "tweet": {
    "textJP": "",
    "textCN": "",
    "hashtags": ["1", "2"]
  },
  "comments": [
    {
      "authorType": "idol|fan|anti",
      "authorId": "(idol)",
      "authorName": "",
      "textJP": "",
      "textCN": ""
    }
  ]${
    params.includeAntiFan
      ? `,
  "rhythmIncrease": 1-10  // `
      : ''
  }
}
\`\`\``;
}

