/**
 * 
 */
export function getTwestaUserInteractionPrompt(params: {
  interactionType: 'comment' | 'reply' | 'retweet';
  originalTweet: { author: string; text: string };
  userText: string;
  replyTarget?: { name: string; text: string };
  idolId: string;
}): string {
  const idolName = getCharacterName(params.idolId);

  const interactionDesc = {
    comment: '',
    reply: `${params.replyTarget?.name}`,
    retweet: '',
  };

  return `# 

## 
- : ${interactionDesc[params.interactionType]}
- : ${idolName}

## 
${params.originalTweet.author}: ${params.originalTweet.text}

${params.replyTarget ? `## \n${params.replyTarget.name}: ${params.replyTarget.text}\n` : ''}

## ${params.interactionType === 'retweet' ? '' : ''}
${params.userText}

## 
1. ****
2. 
3. : 1-2

## JSON
\`\`\`json
{
  "replies": [
    {
      "authorType": "idol|fan",
      "authorId": "(idol)",
      "authorName": "",
      "textJP": "",
      "textCN": "",
      "replyTo": "producer|"
    }
  ]
}
\`\`\``;
}

