/**
 * HookManager - 
 * 
 */
import { RuleEvaluator } from './RuleEvaluator';
// ==================== HookManager  ====================
export class HookManager {
    hooks = new Map();
    ruleEvaluator;
    constructor(ruleEvaluator) {
        this.ruleEvaluator = ruleEvaluator ?? new RuleEvaluator();
    }
    // ====================  ====================
    /**
     *  Hook
     * @param hookDef Hook 
     * @param currentTurn EV3:  play_turn 
     */
    register(hookDef, currentTurn = 1) {
        //  ID 
        if (this.hooks.has(hookDef.id)) {
            console.log(` [HookManager] Hook: ${hookDef.id}`);
        }
        const instance = {
            ...hookDef,
            remaining_turns: hookDef.duration_turns ?? -1, // -1 
            trigger_count: 0,
            is_active: true,
            play_turn: currentTurn, // EV3: 
        };
        this.hooks.set(hookDef.id, instance);
        console.log(` [HookManager] Hook: ${hookDef.id} (${hookDef.trigger}) [play_turn=${currentTurn}]`);
    }
    /**
     *  Hook
     */
    unregister(hookId) {
        if (this.hooks.has(hookId)) {
            this.hooks.delete(hookId);
            console.log(` [HookManager] Hook: ${hookId}`);
            return true;
        }
        return false;
    }
    /**
     *  Hook
     */
    pause(hookId) {
        const hook = this.hooks.get(hookId);
        if (hook) {
            hook.is_active = false;
            console.log(` [HookManager] Hook: ${hookId}`);
        }
    }
    /**
     *  Hook
     */
    resume(hookId) {
        const hook = this.hooks.get(hookId);
        if (hook) {
            hook.is_active = true;
            console.log(` [HookManager] Hook: ${hookId}`);
        }
    }
    // ====================  ====================
    /**
     *  Hook
     * @returns 
     */
    trigger(hookType, context) {
        const actions = [];
        for (const [id, hook] of this.hooks) {
            // 
            if (hook.trigger !== hookType)
                continue;
            // 
            if (!hook.is_active)
                continue;
            // 
            if (hook.remaining_turns === 0)
                continue;
            // 
            if (hook.max_triggers && hook.trigger_count >= hook.max_triggers) {
                console.log(` [HookManager] Hook: ${id}`);
                continue;
            }
            // 
            if (hook.condition) {
                // EV3:  play_turn 
                const contextWithPlayTurn = {
                    ...context,
                    play_turn: hook.play_turn,
                };
                const conditionMet = this.ruleEvaluator.evaluateCondition(hook.condition, contextWithPlayTurn);
                if (!conditionMet) {
                    console.log(` [HookManager] Hook: ${id}`);
                    continue;
                }
            }
            // 
            console.log(` [HookManager] Hook: ${id}`);
            actions.push(...hook.actions);
            hook.trigger_count++;
        }
        return actions;
    }
    // ====================  ====================
    /**
     *  Hook 
     */
    onTurnEnd() {
        const expiredHooks = [];
        for (const [id, hook] of this.hooks) {
            if (hook.remaining_turns > 0) {
                hook.remaining_turns--;
                if (hook.remaining_turns === 0) {
                    expiredHooks.push(id);
                }
            }
        }
        //  Hook
        for (const id of expiredHooks) {
            this.hooks.delete(id);
            console.log(` [HookManager] Hook: ${id}`);
        }
    }
    // ====================  ====================
    /**
     *  Hook
     */
    getActiveHooks() {
        return Array.from(this.hooks.values()).filter(h => h.is_active);
    }
    /**
     *  Hook 
     */
    getHookCountByType(hookType) {
        return Array.from(this.hooks.values()).filter(h => h.trigger === hookType && h.is_active).length;
    }
    /**
     *  ID  Hook
     */
    hasHook(hookId) {
        return this.hooks.has(hookId);
    }
    // ==================== T8:  ====================
    /**
     * T8: 
     * @param card 
     */
    registerIntrinsicHooks(card) {
        const hooks = card.engine_data?.intrinsic_hooks ?? [];
        if (hooks.length === 0)
            return;
        for (const hookDef of hooks) {
            const intrinsicId = `${card.id}::intrinsic::${hookDef.id}`;
            const instance = {
                ...hookDef,
                id: intrinsicId,
                remaining_turns: -1, // 
                trigger_count: 0,
                is_active: true,
                play_turn: 0, // 
                _cardId: card.id, //  ID 
            };
            this.hooks.set(intrinsicId, instance);
            console.log(` [HookManager] : ${intrinsicId}`);
        }
    }
    /**
     * T8:  Hook
     * @param cardId  ID
     */
    unregisterCardHooks(cardId) {
        const toRemove = [];
        for (const [hookId, hook] of this.hooks) {
            //  Hook ID 
            if (hookId.startsWith(`${cardId}::intrinsic::`)) {
                toRemove.push(hookId);
            }
            //  _cardId 
            if (hook._cardId === cardId) {
                toRemove.push(hookId);
            }
        }
        for (const hookId of toRemove) {
            this.hooks.delete(hookId);
            console.log(` [HookManager] : ${hookId}`);
        }
    }
    // ====================  ====================
    /**
     *  Hook
     */
    reset() {
        this.hooks.clear();
        console.log(' [HookManager] Hook');
    }
    // ====================  ====================
    /**
     *  Hook 
     */
    debugPrint() {
        console.log(' [HookManager] Hook:');
        for (const [id, hook] of this.hooks) {
            console.log(`  - ${id}: ${hook.trigger}, active=${hook.is_active}, ` +
                `remaining=${hook.remaining_turns}, triggered=${hook.trigger_count}`);
        }
    }
}
// 
export const hookManager = new HookManager();
