        /**
         * 
         */
        function parseGroupResponse(responseText) {
            try {
                const jsonMatch = responseText.match(/```json\s*([\s\S]*?)\s*```/);
                const jsonStr = jsonMatch ? jsonMatch[1] : responseText;
                const parsed = JSON.parse(jsonStr.trim());
                if (parsed.replies && Array.isArray(parsed.replies)) {
                    return parsed.replies;
                }
                return [];
            }
            catch (error) {
                console.error('[Chain] :', error);
                return null;
            }
        }
        /**
         * 
         * @param chatId  activeChat
         * @param event 
         */
        function toggleFavorite(chatId, event) {
            event?.stopPropagation(); // 
            const targetId = chatId ?? activeChatId.value;
            const chat = chatList.value.find(c => c.id === targetId);
            if (chat) {
                chat.isFavorite = !chat.isFavorite;
                saveChatHistory();
                console.log(`[Chain] ${chat.name} : ${chat.isFavorite ? '' : ''}`);
            }
        }
        function scrollToBottom() {
            nextTick(() => {
                if (chatRef.value) {
                    chatRef.value.scrollTop = chatRef.value.scrollHeight;
                }
            });
        }
        function getCurrentTime() {
            const now = new Date();
            return now.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
        }
        function sendMessage() {
            const hasText = inputText.value.trim();
            const hasImage = pendingImage.value;
            if ((!hasText && !hasImage) || !activeChatId.value || isGenerating.value)
                return;
            const chat = chatList.value.find(c => c.id === activeChatId.value);
            if (!chat)
                return;
            const nowTime = getCurrentTime();
            // 
            chat.messages.push({
                text: inputText.value,
                isMe: true,
                time: nowTime,
                image: hasImage || undefined,
            });
            chat.lastMsg = hasImage ? hasText || '[]' : inputText.value;
            chat.lastTime = nowTime;
            const userMessage = inputText.value;
            const userImage = hasImage || undefined; // 
            inputText.value = '';
            pendingImage.value = null; // 
            showStickerPanel.value = false;
            scrollToBottom();
            saveChatHistory();
            //  AI 
            if (userMessage.trim() || userImage) {
                generateAIReply(chat, userMessage, userImage);
            }
        }
        /**
         *  AI 
         */
        async function generateAIReply(chat, userMessage, userImage) {
            isGenerating.value = true;
            try {
                // 
                const chatHistory = ChainPromptManager.formatChatHistory(chat.messages.map(msg => ({
                    time: msg.time,
                    sender: msg.isMe ? 'user' : 'idol',
                    content: msg.text,
                })), chat.name);
                // 
                const variables = {
                    idolName: chat.name,
                    currentTime: ChainPromptManager.getCurrentTimeString(),
                    chatHistory,
                };
                // 
                const chainOfThought = ChainOfThoughtManager.getChain(ChainOfThoughtMode.CHAIN_REPLY);
                const prompt = ChainPromptManager.getPrompt(ChainMessageMode.REPLY, variables);
                //  =  + 
                const systemPrompt = chainOfThought + '\n\n' + prompt;
                // 
                let userInputText = userMessage || '()';
                if (userImage) {
                    userInputText += '\n\n ';
                    console.log('[Chain] ');
                }
                //  API 
                const customApi = getGenerateApiConfig();
                let responseText = '';
                if (customApi) {
                    //  API
                    responseText = await callCustomApi(systemPrompt, userInputText, customApi, userImage);
                }
                else {
                    //  API
                    responseText = await callMainApi(systemPrompt, userInputText, userImage);
                }
                // 
                const parsed = ChainPromptManager.parseResponse(responseText);
                if (parsed && parsed.messages.length > 0) {
                    //  AI 
                    for (const msg of parsed.messages) {
                        const nowTime = getCurrentTime();
                        chat.messages.push({
                            text: msg.contentCN, // 
                            textJP: msg.contentJP, // 
                            isMe: false,
                            time: nowTime,
                            sticker: msg.sticker ? getStickerUrl(msg.sticker) : undefined,
                        });
                        chat.lastMsg = msg.contentCN;
                        chat.lastTime = nowTime;
                    }
                }
                else {
                    // 
                    chat.messages.push({
                        text: '',
                        isMe: false,
                        time: getCurrentTime(),
                    });
                }
                saveChatHistory(); // 
                scrollToBottom();
            }
            catch (error) {
                console.error('[Chain] AI :', error);
                // 
                chat.messages.push({
                    text: '',
                    isMe: false,
                    time: getCurrentTime(),
                });
                saveChatHistory();
                scrollToBottom();
            }
            finally {
                isGenerating.value = false;
            }
        }
        /**
         * 
         */
        function startEditMessage(index, text) {
            editingMessageIndex.value = index;
            editingText.value = text;
        }
        /**
         * 
         */
        function saveEditMessage(index) {
            const chat = activeChat.value;
            if (!chat || !editingText.value.trim())
                return;
            chat.messages[index].text = editingText.value.trim();
            // 
            if (index === chat.messages.length - 1) {
                chat.lastMsg = editingText.value.trim();
            }
            editingMessageIndex.value = null;
            editingText.value = '';
            saveChatHistory();
        }
        /**
         * 
         */
        function cancelEdit() {
            editingMessageIndex.value = null;
            editingText.value = '';
        }
        /**
         * 
         */
        function deleteMessage(index) {
            const chat = activeChat.value;
            if (!chat)
                return;
            chat.messages.splice(index, 1);
            // 
            if (chat.messages.length > 0) {
                const lastMsg = chat.messages[chat.messages.length - 1];
                chat.lastMsg = lastMsg.text;
                chat.lastTime = lastMsg.time;
            }
            else {
                chat.lastMsg = '';
                chat.lastTime = '';
            }
            saveChatHistory();
        }
