import $ from 'jquery';
import { createPinia } from 'pinia';
import * as PIXI from 'pixi.js';
import { createApp } from 'vue';
import './/.scss';
import MainPage from './/.vue';

//  v8-spine37.js 
import v8Spine37Script from './v8-spine37.js?raw';

//   v8-spine37.js  PIXI  window 
// v8-spine37.js  window.PIXI 
(window as any).PIXI = PIXI;
console.log(' PIXI  window:', (window as any).PIXI);

//  v8-spine37.js 
const scriptElement = document.createElement('script');
scriptElement.textContent = v8Spine37Script;
document.head.appendChild(scriptElement);
console.log(' v8-spine37.js ');
console.log(' PIXI.Spine37:', (window as any).PIXI?.Spine37);

// DOM
$(() => {
  // Vue
  const app = createApp(MainPage);

  // Pinia
  const pinia = createPinia();
  app.use(pinia);

  // 
  app.mount('#app');

  console.log(' ');
});

// 
$(window).on('pagehide', () => {
  console.log(' ');
});
