/**
 * StateManager - 
 *  BuffsTags
 */

import type { StandardBuffId, VisualHint } from './types';

// ==================== Buff  ====================

export interface BuffInstance {
  id: StandardBuffId | string;
  name: string;
  stacks: number; // 
  duration: number; // -1 
  category: 'positive' | 'negative';
  visual_hint?: VisualHint; // AI  Buff 
  multiplier?: number; // Buff  1.0
  source?: string; //  ID  ID
  addedThisTurn?: boolean; // 
  decay_per_turn?: number; // 
}

// ==================== Tag  ====================

export interface TagInstance {
  name: string; // 
  duration: number; // -1 
  visual_hint?: VisualHint;
  source?: string;
}

// ====================  Buff  ====================

const STANDARD_BUFF_CONFIG: Record<
  StandardBuffId,
  {
    name: string;
    category: 'positive' | 'negative';
  }
> = {
  GoodCondition: { name: '', category: 'positive' },
  ExcellentCondition: { name: '', category: 'positive' },
  Concentration: { name: '', category: 'positive' },
  Motivation: { name: '', category: 'positive' },
  GoodImpression: { name: '', category: 'positive' },
  StaminaReduction: { name: '', category: 'positive' },
  AlloutState: { name: '', category: 'positive' },
  ConserveState: { name: '', category: 'positive' },
  ResoluteState: { name: '', category: 'positive' },
  // ===== 1 =====
  AllPower: { name: '', category: 'positive' },
  Heat: { name: '', category: 'positive' },
  StaminaCut: { name: '', category: 'positive' },
  StaminaIncrease: { name: '', category: 'negative' },
  StaminaExtra: { name: '', category: 'negative' },
  ScoreFinalMultiplier: { name: '', category: 'positive' },
  // ===== :  =====
  ScoreBonus: { name: '', category: 'positive' },
  GoodImpressionBonus: { name: '', category: 'positive' },
};

// ==================== StateManager  ====================

export class StateManager {
  private buffs: Map<string, BuffInstance> = new Map();
  private tags: Map<string, TagInstance> = new Map();
  private stateSwitchCount: Record<string, number> = {}; // EV1: per-state 
  private stateSwitchCountTotal: number = 0; // EV1: 
  private buffGainMultipliers: Map<string, number> = new Map(); // T-B3: 
  private buffEffectMultipliers: Map<string, number> = new Map(); // T-B4: 

  /**
   *  Buff
   * Buff  duration
   *
   * :
   * 1) duration > 0  Buff  duration stacks
   * 2) decay_per_turn  duration = -1  Buff 
   */
  public addBuff(
    buffId: StandardBuffId | string,
    stacks: number = 1,
    duration: number = -1,
    visualHint?: VisualHint,
    source?: string,
    decayPerTurn?: number,
  ): void {
    const existing = this.buffs.get(buffId);

    if (existing) {
      //  (Buffdurationstacks)
      if (duration > 0) {
        // : T-B1 max()  +=
        existing.duration += duration;
        existing.addedThisTurn = true; // 
      } else {
        // : T-B3: 
        const gainMultiplier = this.getBuffGainMultiplier(buffId);
        const actualStacks = Math.ceil(stacks * gainMultiplier);
        existing.stacks += actualStacks;
      }
      // 
      if (decayPerTurn !== undefined) {
        existing.decay_per_turn = decayPerTurn;
      }
      console.log(` [StateManager] Buff: ${buffId}  ${existing.stacks}, ${existing.duration}`);
    } else {
      //  Buff
      const config = STANDARD_BUFF_CONFIG[buffId as StandardBuffId];
      const newBuff: BuffInstance = {
        id: buffId,
        name: config?.name ?? buffId,
        stacks,
        duration,
        category: config?.category ?? 'positive',
        visual_hint: visualHint,
        multiplier: 1.0,
        source,
        addedThisTurn: duration > 0, // Buff
        decay_per_turn: decayPerTurn,
      };
      this.buffs.set(buffId, newBuff);
      console.log(
        ` [StateManager] Buff: ${buffId} (${stacks}, ${duration}${decayPerTurn ? `, ${decayPerTurn}` : ''})`,
      );
    }
  }

  /**
   *  Buff
   */
  public removeBuff(buffId: string, stacks?: number): boolean {
    const existing = this.buffs.get(buffId);
    if (!existing) return false;

    if (stacks === undefined || stacks >= existing.stacks) {
      // 
      this.buffs.delete(buffId);
      console.log(` [StateManager] Buff: ${buffId}`);
    } else {
      // 
      existing.stacks -= stacks;
      console.log(` [StateManager] Buff: ${buffId}  ${existing.stacks}`);
    }
    return true;
  }

  /**
   *  Buff 
   */
  public getBuffStacks(buffId: string): number {
    return this.buffs.get(buffId)?.stacks ?? 0;
  }

  /**
   *  Buff 
   */
  public getBuffDuration(buffId: string): number {
    return this.buffs.get(buffId)?.duration ?? 0;
  }

  /**
   *  Buff
   */
  public hasBuff(buffId: string): boolean {
    return this.buffs.has(buffId);
  }

  /**
   *  Buff
   */
  public getAllBuffs(): BuffInstance[] {
    return Array.from(this.buffs.values());
  }

  /**
   * T-B3:  Buff 
   */
  public setBuffGainMultiplier(buffId: string, multiplier: number): void {
    this.buffGainMultipliers.set(buffId, multiplier);
    const buff = this.buffs.get(buffId);
    if (buff) {
      buff.multiplier = multiplier;
    }
    console.log(` [StateManager] : ${buffId}  ${multiplier}x`);
  }

  /**
   * T-B3:  Buff 
   */
  public getBuffGainMultiplier(buffId: string): number {
    return this.buffGainMultipliers.get(buffId) ?? 1.0;
  }

  /**
   * T-B4:  Buff N
   */
  public setBuffEffectMultiplier(buffId: string, multiplier: number): void {
    this.buffEffectMultipliers.set(buffId, multiplier);
    console.log(` [StateManager] : ${buffId}  ${multiplier}x`);
  }

  /**
   * T-B4:  Buff 
   */
  public getBuffEffectMultiplier(buffId: string): number {
    return this.buffEffectMultipliers.get(buffId) ?? 1.0;
  }

  /**
   * T-B2:  Buff  N  max 
   */
  public ensureBuffTurns(buffId: string, turns: number): void {
    const existing = this.buffs.get(buffId);
    if (existing) {
      if (turns > existing.duration) {
        existing.duration = turns;
        console.log(` [StateManager] Buff: ${buffId}  ${turns}`);
      }
    } else {
      this.addBuff(buffId, 1, turns);
    }
  }

  // ==================== Tag  ====================

  /**
   *  Tag
   */
  public addTag(tag: string, duration: number = -1, visualHint?: VisualHint, source?: string): void {
    // 
    const fullTag = this.normalizeTagName(tag);

    const existing = this.tags.get(fullTag);
    if (existing) {
      // 
      if (duration > existing.duration) {
        existing.duration = duration;
      }
    } else {
      this.tags.set(fullTag, {
        name: fullTag,
        duration,
        visual_hint: visualHint,
        source,
      });
      console.log(` [StateManager] Tag: ${fullTag}`);
    }
  }

  /**
   *  Tag
   */
  public removeTag(tag: string): boolean {
    const fullTag = this.normalizeTagName(tag);
    if (this.tags.has(fullTag)) {
      this.tags.delete(fullTag);
      console.log(` [StateManager] Tag: ${fullTag}`);
      return true;
    }
    return false;
  }

  /**
   *  Tag
   */
  public hasTag(tag: string): boolean {
    const fullTag = this.normalizeTagName(tag);
    return this.tags.has(fullTag);
  }

  /**
   *  Tag 
   */
  public getAllTags(): string[] {
    return Array.from(this.tags.keys());
  }

  /**
   *  Tag 
   */
  public getAllTagInstances(): TagInstance[] {
    return Array.from(this.tags.values());
  }

  /**
   *  Tag 
   */
  private normalizeTagName(tag: string): string {
    // 
    if (tag.includes(':')) {
      return tag;
    }
    //  ai: AI 
    return `ai:${tag}`;
  }

  // ====================  ====================

  /**
   * 
   */
  public recordStateSwitch(fromState: string | null, toState: string): void {
    // EV1:  per-state 
    this.stateSwitchCount[toState] = (this.stateSwitchCount[toState] ?? 0) + 1;
    this.stateSwitchCountTotal++;
    console.log(
      ` [StateManager] : ${fromState ?? 'null'}  ${toState} (${toState}=${this.stateSwitchCount[toState]}, =${this.stateSwitchCountTotal})`,
    );
  }

  /**
   *  (per-state )
   */
  public getStateSwitchCount(): Record<string, number> {
    return { ...this.stateSwitchCount };
  }

  /**
   * 
   */
  public getStateSwitchCountTotal(): number {
    return this.stateSwitchCountTotal;
  }

  // ====================  ====================

  /**
   * 
   *  Buff 
   */
  public onTurnEnd(): void {
    //  Buff 
    for (const [id, buff] of this.buffs) {
      //  Buff 
      if (buff.addedThisTurn) {
        buff.addedThisTurn = false;
        continue;
      }
      if (buff.duration > 0) {
        buff.duration--;
        if (buff.duration === 0) {
          this.buffs.delete(id);
          console.log(` [StateManager] Buff: ${id}`);
        }
      }
      //  (decay_per_turn)
      if (buff.decay_per_turn && buff.stacks > 0) {
        buff.stacks -= buff.decay_per_turn;
        if (buff.stacks <= 0) {
          this.buffs.delete(id);
          console.log(` [StateManager] Buff: ${id}`);
        } else {
          console.log(` [StateManager] Buff: ${id} ${buff.stacks}`);
        }
      }
    }

    //  Tag 
    for (const [name, tag] of this.tags) {
      if (tag.duration > 0) {
        tag.duration--;
        if (tag.duration === 0) {
          this.tags.delete(name);
          console.log(` [StateManager] Tag: ${name}`);
        }
      }
    }
  }

  // ====================  ====================

  /**
   * 
   */
  public reset(): void {
    this.buffs.clear();
    this.tags.clear();
    this.buffGainMultipliers.clear(); // T-B3: 
    this.buffEffectMultipliers.clear(); // T-B4: 
    this.stateSwitchCount = {}; // EV1: per-state 
    this.stateSwitchCountTotal = 0; // EV1: 
    console.log(' [StateManager] ');
  }

  // ====================  ====================

  /**
   * T-10: 
   *  (when)
   */
  public toBuffRawRecord(): Record<string, number> {
    const record: Record<string, number> = {};
    for (const [id, buff] of this.buffs) {
      record[id] = buff.stacks; // 
    }
    return record;
  }

  /**
   * T-10: 
   *   effectMultiplier
   */
  public toBuffEffectiveRecord(): Record<string, number> {
    const record: Record<string, number> = {};
    for (const [id, buff] of this.buffs) {
      const effectMultiplier = this.getBuffEffectMultiplier(id);
      record[id] = buff.stacks * effectMultiplier;
    }
    return record;
  }

  /**
   *  JSON Logic
   * @deprecated  toBuffRawRecord  toBuffEffectiveRecord
   * T-B4: 
   */
  public toBuffRecord(): Record<string, number> {
    // T-10: 
    return this.toBuffEffectiveRecord();
  }
}

// 
export const stateManager = new StateManager();
