// ============ Types ============
export interface StreamVideo {
  id: string;
  title: string;
  thumbnail: string;
  videoUrl: string;
  duration: string;
  releaseDate: string;
  category: 'idol' | 'unit' | 'etc';
  unlocked: boolean;
  description?: string;
  idolIds?: string[]; // ID
  unitId?: string; // ID
}

export interface StreamCategory {
  id: 'idol' | 'unit' | 'etc';
  label: string;
  labelJP: string;
}

// ============ Constants ============
export const STREAM_CATEGORIES: StreamCategory[] = [
  { id: 'idol', label: '', labelJP: '' },
  { id: 'unit', label: '', labelJP: '' },
  { id: 'etc', label: '', labelJP: '' },
];

// URL
