export function getSpineDataById(id: string): CharacterSpineData | undefined {
  return SPINE_CHARACTERS.find(char => char.id === id);
}

export function getSpineDataByJapaneseName(japaneseName: string): CharacterSpineData | undefined {
  return SPINE_CHARACTERS.find(char => char.japaneseName === japaneseName);
}

export function getSpineId(japaneseName: string, cardName: string): string {
  return `${japaneseName}_${cardName}`;
}

/** Spine */
export function getTotalSpineCards(): number {
  return SPINE_CHARACTERS.reduce((sum, char) => sum + char.cards.length, 0);
}
