// CDNURL
const CDN_BASE = 'https://283pro.site/shinycolors';
const UNIT_ICON_BASE = `${CDN_BASE}/`;

/**
 * URL
 */
function buildUnitIconUrl(unitName: string): string {
  return `${UNIT_ICON_BASE}/${encodeURIComponent(unitName)}.webp`;
}

export interface IdolUnit {
  id: string;
  name: string;
  nameJa: string; // 
  icon: string; // emoji
  iconUrl: string; // URL
  members: string[]; // 
}

