                    originalEngineData: JSON.stringify(skill.engine_data || {}),
                    repairIssue: repairIssueInput.value,
                    streaming: true,
                };
                const result = await generateSkillCardRepair(options);
                if (result.success && result.skillCard) {
                    //  DisplayCard.skill 
                    const newSkill = {
                        ...skill, //  ()
                        ...result.skillCard, // 
                        //  description 
                        description: skill.description,
                        //  effect  (DisplayCard.skill  effect )
                        effect: result.skillCard.effect_before || skill.effect || '',
                        //  engine_data 
                        engine_data: result.skillCard.engine_data,
                        //  repair_meta 
                        repair_meta: result.skillCard.repair_meta,
                    };
                    // 
                    selectedCard.value.skill = newSkill;
                    // 
                    const updateCardSkill = (cardsList) => {
                        const index = cardsList.findIndex(c => c.fullCardName === card.fullCardName);
                        if (index !== -1) {
                            cardsList[index].skill = newSkill;
                        }
                    };
                    updateCardSkill(allCards.value);
                    updateCardSkill(displayCards.value);
                    // 
                    saveSkillCard(card.fullCardName, newSkill);
                    toastr.success('', '', { timeOut: 1500 });
                    closeRepairModal();
                }
                else {
                    repairError.value = result.error || '';
                }
            }
            catch (error) {
                console.error(':', error);
                repairError.value = error instanceof Error ? error.message : String(error);
            }
            finally {
                isRepairing.value = false;
            }
        }
        /**
         * 
         */
        function copyRawOutput() {
            if (!rawAIOutput.value)
                return;
            navigator.clipboard
                .writeText(rawAIOutput.value)
                .then(() => {
                toastr.success('', '', { timeOut: 1500 });
            })
                .catch(err => {
                console.error(':', err);
                toastr.error('', '', { timeOut: 1500 });
            });
        }
        /**
         * 
         */
        function toggleSkillEnhancement() {
            isSkillEnhanced.value = !isSkillEnhanced.value;
        }
        /**
         * 
         */
        function handleIconError(event) {
            const img = event.target;
            // 
            img.style.display = 'none';
            console.warn(':', img.src);
        }
        /**
         * 
         */
        function formatEffectEntry(entry) {
            if (!entry || !entry.effect)
                return '';
            return entry.effect;
        }
        /**
         * 
         */
        function formatRestrictions(restrictions) {
            if (!restrictions)
                return '';
            const parts = [];
            // 
            if (restrictions.isDuplicatable === false) {
                parts.push('');
            }
            // 
            if (restrictions.usesPerBattle === 1) {
                parts.push('1');
            }
            return parts.join('  '); // 
        }
        // 
        onMounted(async () => {
            // 
            try {
                userData.value = await loadUserData();
            }
            catch (error) {
                console.error(':', error);
            }
            // 
            await initializeCards();
            toastr.success('', '', { timeOut: 2000 });
        });
